.button_wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.inc_button_wrapper {
    display: grid;
    justify-content: center;
    align-items: center;
}

.inc_button {
    background: #e2e2e2;
    border: none;
    width: 100px;
    height: 50px;
    border-radius: 2%;
    font-size: 20px;
    transition: all 200ms;
}

.inc_button:hover {
    cursor: pointer;
    transition: all 200ms;
    transform: scale(1.1)
}

.reset_button_wrapper {
    display: grid;
    justify-content: center;
    align-items: center;
}

.reset_button {
    background: #e2e2e2;
    width: 100px;
    height: 50px;
    border: none;
    border-radius: 2%;
    font-size: 20px;
    transition: all 200ms;
}

.reset_button:hover {
    cursor: pointer;
    transition: all 200ms;
    transform: scale(1.1)
}